import React, { createContext } from 'react';

export const Context = createContext();

export const Provider = props => {
    let auth= "Sign Up";
    
    const data = {
        id: "init id",
        name: localStorage.getItem("name"),
        authenticated: localStorage.getItem("authenticated")
    };

    let players = [];

    let fetchplayers = false;

    let propping_id = "";

    const api_url = window.location.origin;
    //const api_url = "http://dabbscreen.com/";
    //const api_url = "https://shuffling2.appdeployments.now.sh";
    //const api_url = "https://guarded-caverns-33102.herokuapp.com";

        return (
            <Context.Provider value={[data, (id, auth) => {data.id = id; data.authenticated = auth;}, players, fetchplayers, propping_id, auth, (name) => {data.name = name;}, api_url]} >
                {props.children}
            </Context.Provider>
        );

}