import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './propping.css'

class Staging extends Component {

    constructor(props){
        super(props);

        this.state = {
            routes_preurl: "",
        }
    }

    componentDidMount(){
        document.getElementById("updateHeaderRight").click();
    }

    render(){
        return (
            <section>
            <div className="play-container">  
                <div className="play-instructions">
                    <h6>Join a conversation of up to 10 people. Are you set?!</h6>
                    <Link to={ this.state.routes_preurl+"/play" }>
                    <button className="btn btn-primary">
                        Enter a New Conversation
                    </button>
                    </Link>
                </div>
            </div>
            </section>
        );
    }

}

export default Staging;