import React from 'react';
import Header from './components/templates/header';
import Footer from './components/templates/footer';
import Signup from './components/auth/signup';
import Login from './components/auth/login';
import Play from './components/propping/play';
import Staging from './components/propping/staging';
import Terms from './components/terms/terms';
import { Context } from './contextclass';
import {Provider} from './contextclass';
import { Route } from 'react-router-dom';
import './App.css';

function App() {
  const routes_preurl = "";
  
  return (
    <Provider>
    <div className="app">
      <Header />

      <Route exact path={ routes_preurl+"/" } component={Login} />
      <Route path={ routes_preurl+"/signup" } component={Signup} />
      <Route path={ routes_preurl+"/play" } component={Play} />
      <Route path={ routes_preurl+"/staging" } component={Staging} />
      <Route path={ routes_preurl+"/terms" } component={Terms} /> 

      <Footer />
    </div>
    </Provider>
  );
}

export default App;