import React from 'react';
import {Link} from 'react-router-dom';
import './templates.css'

const Footer = () => (
    <div className="footer">
        <footer className="footer text-center">
            Invite your people from Facebook and Twitter to join DabbStream.<br />
                <div className="fb-share-button" data-href="http://dabbstream.com" data-layout="button_count" data-size="small"><Link target="_blank" href="https://www.facebook.com/sharer/sharer.php?u=https%3A%2F%2Fdevelopers.facebook.com%2Fdocs%2Fplugins%2F&amp;src=sdkpreparse" className="fb-xfbml-parse-ignore">Share</Link></div>
                <div>
                { "   " }<Link href="https://twitter.com/share?ref_src=twsrc%5Etfw" className="twitter-share-button" data-show-count="false">Tweet</Link><br />
                </div>
                    
            DabbStream @2019 <Link to="/terms">DabbStream's Terms of Use and Privacy Policy</Link>
        </footer>
    </div>
);

export default Footer;