import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Comment from './comment'
import Chat from './chat';
import {Context} from '../../contextclass';
import './propping.css'

class Play extends Component {
    constructor(props){
        super(props);

        this.state = {
            routes_preurl: "",
            id: 1,
            email: "email1",
            numberofplayers: 0,
            playerids: [],
            players: [],
            time: 60,
            propping_id: 0,
            fetchplayers: false,
            creditearned: 0,
            winner: { name:"" },
            prize: 0,
            prize_w_currency: "",
            new_prize: 0,
            credit_now_available: 0
        }

    }

    static contextType = Context;

    componentDidMount(){
        this.addplayer();
    }

    componentWillUnmount(){
        this.leaveConversation( this.state.propping_id );
    }

    addplayer = () => {
        const id = localStorage.getItem("id");
        const name = localStorage.getItem("name");
        fetch(this.context[7]+'/addplayer', {
            method: 'POST',
            body: JSON.stringify({ id: id, name: name }),
            headers: { 
                'Content-Type': 'application/json',
                'Accept': 'application/json' 
            }
        })
        .then(res => res.json())
        .then((body) => (body.response_msg === "Player Added" ? this.checkPropping( body.propping_id ) : console.log("could not add player!")));
    }

    isProppingReady = ( propping_id, numberofplayers ) => {
        if(numberofplayers < 1){ 
            this.setState({ numberofplayers: numberofplayers })
            setTimeout( this.checkPropping( propping_id ), 2000 );
        }else{
            this.context[3] = true;
            this.context[4] = propping_id;
            this.setState({ propping_id: propping_id });
            this.setState({ fetchplayers: true });
        }
    }

    checkPropping = ( propping_id ) => {
        fetch(this.context[7]+'/playersfrompropping', {
            method: 'POST',
            body: JSON.stringify({ propping_id: propping_id }),
            headers: { 
                'Content-Type': 'application/json',
                'Accept': 'application/json' 
            }
        })
        .then(res => res.json())
        .then((body) => (body.response_msg === "Here are the players!" ? this.isProppingReady( body.players[0]._id, body.players[0].numberofplayers ) : console.log("could not retrieve players")));
    }  
    
    leaveConversation = ( propping_id ) => {
        const id = localStorage.getItem("id");
        fetch(this.context[7]+'/leaveconversation', {
            method: 'POST',
            body: JSON.stringify({ propping_id: propping_id, id: id }),
            headers: { 
                'Content-Type': 'application/json',
                'Accept': 'application/json' 
            }
        })
        .then(res => res.json())
        .then((body) => (body.response_msg === "Left conversation!" ? console.log("Left conversation successfully!") : console.log("could not leave conversation")));
    }

    render(){ 
            if( this.state.fetchplayers === true ){
                return ( 
                    <section>
                    <div className="play-container">  
                        <div className="play-instructions">
                            <Link to={ this.state.routes_preurl+"/staging" }>
                                <button className="btn btn-outline-primary">
                                    Leave Conversation
                                </button>
                            </Link>
                            <br /><br />
                        </div>
                        <div className="play-instructions">
                        <h6>We're set baby... Say hi!</h6>
                        </div>
                        <div className="play-comment">
                            <Comment name={ this.state.propping_id } />
                        </div>
                        <div className="play-chat">
                            <Chat name={ this.state.propping_id } />
                        </div>
                    </div>
                    <br /><br /><br /><br />
                    </section>
                );
            }else{
            return (
                <section>
                <div className="play-container">  
                    <div className="play-instructions">
                        <h6>Waiting for { 2-this.state.numberofplayers } other { (2-this.state.numberofplayers) === 1 ? "person" : "people" } to join...</h6>
                            <br /><br />
                            <h6>If it's taking too long, invite your people from Facebook and Twitter to Shuffling.</h6>
                            {/* Leave game */}
                            {/* Facebook and Twitter share buttons */}
                    </div>
                </div>
                </section>
            );
        }
    }
}

export default Play;