import React from 'react';
import './templates.css';
import { Link } from 'react-router-dom';
import HeaderRight from './headerright';
import HeaderMiddle from './headermiddle';


const Header = () => (
    <div className="header">
        <div className="logo">
        <Link  to={ "/" }>
            DabbStream
        </Link>
        </div>
        <HeaderMiddle />
        <HeaderRight />
    </div>
);

export default Header;